import React from 'react';

class Time extends React.Component {

    state = {
        time : []
    }

    tiempo = () => {
        let time = new Date();

        let obj = {
            hours : (time.getHours() < 10) ? '0'+time.getHours() : time.getHours(),
            minutes : (time.getMinutes() < 10) ? '0'+time.getMinutes() : time.getMinutes(),
            seconds : (time.getSeconds() < 10) ? '0'+time.getSeconds() : time.getSeconds(),
            dia : time.toLocaleDateString()
        }

        this.setState({
            time : obj
        });
    }

    componentDidMount() {
        setInterval(this.tiempo, 1000);
    }

    render() {
        return(
            <div className="p-4 flex justify-between items-center">
                <p className="text-center sm:text-2xl md:text-3xl font-bold text-black">{this.state.time.dia}</p>
                <p className="text-center sm:text-2xl md:text-3xl font-bold italic text-black ml-3">{this.state.time.hours} : {this.state.time.minutes} : {this.state.time.seconds} hs</p>
            </div>
        );
    }

}

export default Time;