import React from 'react';
import Cardnews from './components/Cardnews';
import Pags from './components/Pags';
import Time from './components/Time';
import Query from './components/Query';

class App extends React.Component {

  state = {
    news : [],
    page : 1,
    clima : [],
    country : 'ar',
    query : '',
    type : 'top'
  }

  consultarNews = () => {
    
    let obj = {
      top : `https://newsapi.org/v2/top-headlines?country=${this.state.country}&page=${this.state.page}&pageSize=10&q=${this.state.query}&apiKey=71c746f3446c41d09aa42f685d94ff3b`,
      all : `https://newsapi.org/v2/everything?q=${this.state.query}&language=es&sortBy=publishedAt&apiKey=71c746f3446c41d09aa42f685d94ff3b`
    }

    let type = obj.top;

    if(this.state.type === 'all') type = obj.all;
  
    fetch(type)
      .then(response => response.json())
      .then(result => {
        this.setState({
          news : result.articles
        });
      });
  }

  consultarClima = () => {
    let URL = `https://api.openweathermap.org/data/2.5/weather?q=Buenos%20Aires&units=metric&lang=es&APPID=f9c896edd5d21e5641ca7e1d113648c0`;
    fetch(URL)
      .then(response => response.json())
      .then(result => {
        this.setState({
          clima : result
        });
      });
  }

  scroll = () => {
    let ref = document.querySelector('nav');
    ref.scrollIntoView('smooth', 'start');
  }

  getPage = (page) => {
    this.setState({
      page
    }, () => {
      this.consultarNews();
      this.scroll();
    });
  }

  getQuery = (query, country, type) => {
    this.setState({
      query,
      country,
      type
    }, () => {
      this.consultarNews();
      this.scroll();
    });
  }

  componentDidMount() {
    this.consultarClima();
    this.consultarNews();
  }

  mostrarClima = () => {
    let clima = this.state.clima;
    if(clima.length === 0) return null; 
    let icono = `http://openweathermap.org/img/w/${clima.weather[0].icon}.png`;
    return(
      <div className="lg:px-3 flex items-center justify-center text-white font-bold bg-orange-400 shadow-md">
        <div className="flex items-center">
          <p className="mx-2 text-2xl font-bold">{Math.floor(clima.main.temp)} Â°C</p>
          <img src={icono} alt="icono"/>
        </div>
        <div className="p-2">
          <p className="text-2xl">{clima.weather[0].description}</p>
          <p className="text-2xl">Humedad : {clima.main.humidity} %</p>
        </div>
      </div>
    );
  }

  render() {

    return(
      <React.Fragment>

      <nav className="bg-blue-300 shadow-inner shadow-2xl">
        <h1 className="text-5xl text-center text-white font-bold italic pt-3">NOTICIAS ANDY</h1>
        <div className="lg:flex lg:justify-between bg-orange-200">
          <div className="md:flex md:justify-between">
            {this.mostrarClima()}
            <Time />
          </div>
          <div className="flex items-center xl:w-5/12">
            <Query data={this.getQuery}/>
          </div>
        </div>
      </nav>
      <div className="container mx-auto">
          <Cardnews data={this.state.news}/>
          <Pags data={this.getPage} pageActual={this.state.page}/>
      </div>

      </React.Fragment>
      );
  }

}

export default App;
