import React from 'react';

class Pags extends React.Component {

    anterior = () => {
        let page = this.props.pageActual;
        if(page <= 0) return null;
        page -= 1;
        this.props.data(page);
    }

    siguiente = () => {
        let page = this.props.pageActual;
        page += 1;
        this.props.data(page);
    }


    render(){
        return(
            <div className="w-full flex justify-around">
                <button onClick={this.anterior} className="p-4 font-bold text-3xl text-white bg-red-400 rounded-l-lg shadow-md">Anterior</button>
                <button onClick={this.siguiente} className="p-4 font-bold text-3xl text-white bg-green-400 rounded-r-lg shadow-md">Siguiente</button>
            </div>
        );
    }

}

export default Pags;