import React from 'react';

class Query extends React.Component {

    select = React.createRef();
    query = React.createRef();
    type = React.createRef();

    buscar = (e) => {

        e.preventDefault();

        let country = this.select.current.value;
        let query = this.query.current.value;
        let type = this.type.current.value;

        this.props.data(query, country, type);

    }

    render(){
        return(
            <form onSubmit={this.buscar} className="flex justify-center shadow w-full lg:mr-3">
                <input ref={this.query} type="text" className="p-2 bg-orange-100 sm:text-lg md:text-2xl font-bold w-full rounded-l-lg" placeholder="Buscar noticia..."/>
                <select ref={this.select} className="bg-orange-200 font-bold shadow-lg cursor-pointer">
                    <option value="ar">Argentina</option>
                    <option value="ru">Rusia</option>
                    <option value="in">India</option>
                </select>
                <select ref={this.type} className="bg-orange-200 font-bold shadow-lg cursor-pointer">
                    <option value="top">De Hoy</option>
                    <option value="all">Todo</option>
                </select>
                <input type="submit" value="Buscar" className="p-4 cursor-pointer rounded-r-lg font-bold bg-orange-400 text-white text-lg"/>
            </form>
        );
    }

}

export default Query;