import React from 'react';

function Cardnews(props) {

    let news = props.data.map((x, i) => {

        let dateIn = new Date(x.publishedAt);
        let time = dateIn.toLocaleTimeString();
        let date = dateIn.toLocaleDateString();

        let reg = /\[.+?]/g;
        //eslint-disable-next-line
        let string = new String(x.content);
        let content = string.replace(reg, '');

        return(
            <div key={i} className="text-gray-400 bg-gray-800 shadow-lg mt-4 flex flex-col justify-between">
                <a href={x.url}>
                    <img src={x.urlToImage} alt="imagen" className="w-full"/>
                </a>
                <div className="p-2">
                    <p className="text-2xl font-bold">{x.title}</p>
                </div>
                <div>
                    <p className="p-2 text-xl">{x.description}</p>
                    <p className="p-2 my-2 italic text-blue-300">{content}</p>
                </div>
                <div className="p-2 flex flex-col items-center bg-black">
                    <p className="text-xl font-bold">{x.source.name}</p>
                    <p>{date}</p>
                    <p>{time}</p>
                </div>
            </div>
        );
    });

    return(
        <div className="grid md:grid-cols-2 md:gap-5 my-5">
            {news}
        </div>
    );

}

export default Cardnews;